const getAPIBaseURL = () => {
  const apiProtocol = process.env.REACT_APP_API_PROTOCOL || "http";
  const apiHost = process.env.REACT_APP_API_HOST || "localhost";
  const apiPort = process.env.REACT_APP_API_PORT || "3015";

  return `http://api.gotmyring.com/`;
};

const capitalize = (word: string) => {
  let capitalizedWord = "";
  if (word) {
    capitalizedWord = word.substring(0, 1).toUpperCase();

    if (word.length > 1) {
      capitalizedWord += word.substring(1, word.length).toLowerCase();
    }
  }
  return capitalizedWord;
};

export { getAPIBaseURL, capitalize };
