import { useState } from "react";
import { useNavigate } from "react-router-dom";
import Button from "src/components/Button";
import PageWrap from "src/components/PageWrap";
import FormInput from "src/components/form/FormInput";
import FormSelect from "src/components/form/FormSelect";
import { MapTypes } from "src/state/domains/map-types/types";
import { useToast } from "src/utils/useToast";
import { IProject } from "src/state/domains/project/types";
import { ProjectionMethods } from "src/state/domains/projection-methods/types";
import { capitalize } from "src/utils/commonUtils";
import axios from "axios";

const defaultProject: IProject = {
  projectName: "",
  mapType: MapTypes.OPEN_STREET_MAP,
  projectionMethod: ProjectionMethods.WGS84,
};

const tempDataProvidersObj = [
  {
    id: "1",
    name: "John Doe",
  },
  {
    id: "2",
    name: "Alexa",
  },
  {
    id: "3",
    name: "Siri",
  },
  {
    id: "4",
    name: "Nicolai",
  },
];

export default function CreateProject() {
  const { successToast, errorToast } = useToast();
  const navigate = useNavigate();
  const [project, setProject] = useState<IProject>(defaultProject);
  const [isSavingChanges, setIsSavingChanges] = useState(false);

  const handleFormChange = (name: string, value: string) => {
    setProject((prevProject) => ({
      ...prevProject,
      [name]: value,
    }));
  };

  const handleFormSubmit = async () => {
    setIsSavingChanges(true);
    try {
      await axios.post(`/projects`, project);
      successToast("Project Created");
      navigate("/projects");
    } catch {
      errorToast("Failed to create project!");
    } finally {
      setIsSavingChanges(false);
    }
  };

  return (
    <PageWrap title="Create project">
      <div className="bg-white py-6 px-6 w-1/2 space-y-12">
        <form action="" className="w-1/2 space-y-4">
          <FormInput
            lable="project name"
            type="text"
            name="projectName"
            formValues={project}
            placeholder="Project Name"
            onChange={({ target: { name, value } }) =>
              handleFormChange(name, value)
            }
          />
          <FormInput
            lable="Description"
            type="text"
            name="description"
            formValues={project}
            placeholder="Description"
            onChange={({ target: { name, value } }) =>
              handleFormChange(name, value)
            }
          />
          <FormSelect
            lable="Data provider"
            name="dataProvider"
            formValues={project}
            items={tempDataProvidersObj.map((position) => ({
              value: position.name,
              displayValue: position.name,
            }))}
            onChange={({ target: { name, value } }) =>
              handleFormChange(name, value)
            }
          />
          <FormSelect
            lable="Map type"
            name="mapType"
            formValues={project}
            items={Object.keys(MapTypes).map((lb) => ({
              value: lb,
              displayValue: capitalize(lb),
            }))}
            onChange={({ target: { name, value } }) =>
              handleFormChange(name, value)
            }
          />
          <FormSelect
            lable="Projection Method"
            name="projectionMethod"
            formValues={project}
            items={Object.keys(ProjectionMethods).map((lb) => ({
              value: lb,
              displayValue: capitalize(lb),
            }))}
            onChange={({ target: { name, value } }) =>
              handleFormChange(name, value)
            }
          />
        </form>
        <div className="flex justify-end">
          <Button
            onClick={handleFormSubmit}
            isProcessing={isSavingChanges}
            disabled={isSavingChanges}
          >
            Submit
          </Button>
        </div>
      </div>
    </PageWrap>
  );
}
