import React, { useState, useEffect } from 'react';
import dxfParser from 'dxf-parser';
import DXFImport from 'src/components/dxf/DXFImport';
import { useDispatch } from 'react-redux';
import { setFeatures } from 'src/state/slice/geoJsonSlice';
import { IDXFData } from 'src/types/IDXFTypes'; 

type FileUploadProps = {
  dxfFileUrl: string;
  projectionType: string;
};

export const FileUpload: React.FC<FileUploadProps> = ({ dxfFileUrl, projectionType }) => {
  const dispatch = useDispatch();

  useEffect(() => {
    const fetchDXFFile = async () => {
      if (!dxfFileUrl || projectionType === null) return;
      
      try {
        const response = await fetch(dxfFileUrl);
        const fileText = await response.text();
        processDXFContent(fileText, projectionType);
      } catch (error) {
        console.error("Error fetching DXF file:", error);
      }
    };

    fetchDXFFile();
  }, [projectionType]);

  const processDXFContent = (content: string, projectionType: string ) => {
    const parser = new dxfParser();
    const dxfData = parser.parseSync(content);
    
    const geoJSONData = DXFImport(dxfData as unknown as IDXFData, projectionType);  
    dispatch(setFeatures(geoJSONData.features));
  };

    // Render logic for lines or other components...
  return (
    <div>
      {/* Render your lines or any other UI elements here */}
    </div>
  );
};
