import classNames from "classnames";
import { IFormFileUploadProps } from "src/components/types/form/types";

function FormFileUpload({
  lable,
  name,
  onChange,
  formErrors,
  className,
  isUploading,
  isSuccess,
}: IFormFileUploadProps) {
  const hasError = formErrors && formErrors[name]?.length > 0;

  return (
    <div className="flex flex-col gap-y-1 w-full">
      <label htmlFor={name} className="text-blue-500 capitalize">
        {lable}
      </label>
      <div className="relative">
        <input
          type="file"
          id={name}
          name={name}
          onChange={onChange}
          className={classNames(
            "px-2 py-2 w-full border border-blue-500 rounded-md bg-white",
            className && className
          )}
        />
        {isSuccess && (
          <span className="text-green-600 text-sm capitalize float-right mt-1">File successfuly uploaded!</span>
        )}
        {isUploading && (
          <svg
            data-testid="mini-loading-svg"
            className={`animate-spin -ml-1 mr-3 h-5 w-5 absolute right-0 top-3`}
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 24 24"
          >
            <circle
              className="opacity-25"
              cx="12"
              cy="12"
              r="10"
              stroke="currentColor"
              strokeWidth="4"
            />
            <path
              className="opacity-75"
              fill="currentColor"
              d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
            />
          </svg>
        )}
      </div>

      {hasError && (
        <p className="mt-1 text-sm text-red-600 lowercase">
          {formErrors && formErrors[name]}
        </p>
      )}
    </div>
  );
}

export default FormFileUpload;
