import { ChartBarIcon, PlusIcon, StarIcon } from "@heroicons/react/24/solid";
import classNames from "classnames";
import { useEffect } from "react";
import { useParams } from "react-router-dom";
import { useAppDispatch, useAppSelector } from "src/app/hooks";
import { ILayer } from "src/state/domains/layer/types";
import { setIsOpenCreateFormModal } from "src/state/slice/appSlice";
import {
  selectSelectedLayerId,
  selectShouldRefetchLayers,
  setSelectedLayerId,
  setShouldRefetchLayers,
} from "src/state/slice/projectSlice";
import useFetch from "src/utils/request/useFetch";

export default function LayerList() {
  const shouldRefetchLayers = useAppSelector(selectShouldRefetchLayers);
  const dispatch = useAppDispatch();
  const { projectId } = useParams<{ projectId: string }>();
  const selectedLayerId = useAppSelector(selectSelectedLayerId);
  const { data: layers, reFetch } = useFetch({
    url: `/projects/${projectId}/layers`,
  });

  useEffect(() => {
    if (shouldRefetchLayers) {
      reFetch();
      dispatch(setShouldRefetchLayers(false));
    }
  }, [shouldRefetchLayers, reFetch, dispatch]);

  return (
    <div className="pl-4 flex flex-col gap-y-2 h-auto overflow-auto">
      <div className="flex items-center gap-2">
        <ChartBarIcon className="text-white w-7 h-7" />
        <span className="text-white text-lg font-bold">Layers</span>
      </div>
      <div className="flex flex-col gap-3 ml-10">
        <div className="flex flex-col gap-3 max-h-[300px] overflow-auto">
          {Array.isArray(layers) &&
            layers.map((layer: ILayer) => (
              <button
                className={classNames(
                  "text-base text-gray-300 flex items-center gap-3",
                  { "text-white font-semibold": selectedLayerId === layer.id }
                )}
                key={layer.id}
                type="button"
                onClick={() => {
                  dispatch(setSelectedLayerId(layer.id));
                }}
              >
                <StarIcon className="w-4 h-4" />
                {layer.layerName}
              </button>
            ))}
        </div>
        <button
          className="text-gray-200 capitalize text-sm flex items-center gap-1 font-semibold"
          onClick={() => dispatch(setIsOpenCreateFormModal(true))}
        >
          <PlusIcon className="w-5 h-5" />
          Add new layer
        </button>
      </div>
    </div>
  );
}
