import { ChangeEvent, useState } from "react";
import { PlusIcon } from "@heroicons/react/24/solid";
import FormInput from "src/components/form/FormInput";
import FormSelect from "src/components/form/FormSelect";
import { SelectValue } from "src/components/types/form/types";

interface FormInputGroupProps {
  addInputButtonLable: string;
  formInputPlaceholder: string;
  formSelectPlaceholder: string;
  formInputValues: Record<string, any>;
  formSelectItems: SelectValue[];
  formInputLable: string;
  onInputChange: (index: number, event: ChangeEvent<HTMLInputElement>) => void;
  onSelectChange: (
    index: number,
    event: ChangeEvent<HTMLSelectElement>
  ) => void;
}

const FormInputGroup = ({
  addInputButtonLable,
  formInputPlaceholder,
  formSelectPlaceholder,
  formInputValues,
  formSelectItems,
  formInputLable,
  onInputChange,
  onSelectChange,
}: FormInputGroupProps) => {
  const [inputCount, setInputCount] = useState(0);

  const handleAddInputField = () => {
    setInputCount((prevCount) => prevCount + 1);
  };

  return (
    <div>
      {Array.from({ length: inputCount }).map((_, index) => (
        <div key={index} className="flex items-center gap-6 mt-4">
          <div className="flex-1">
            <FormInput
              lable={`${formInputLable} 0${index+1}`}
              name="name"
              placeholder={formInputPlaceholder}
              type="text"
              onChange={(event) => onInputChange(index, event)}
              formValues={formInputValues[index] || {}}
              className="w-full"
              size="tiny"
            />
          </div>
          <div className="flex-1">
            <FormSelect
              name="type"
              lable={formSelectPlaceholder}
              items={formSelectItems}
              placeholder={formSelectPlaceholder}
              onChange={(event) => onSelectChange(index, event)}
              formValues={formInputValues[index] || {}}
            />
          </div>
        </div>
      ))}
      <button
        type="button"
        className="text-blue-500 flex items-center gap-1 mt-2"
        onClick={handleAddInputField}
      >
        <PlusIcon className="w-4 h-4" />
        <span className="capitalize">{addInputButtonLable}</span>
      </button>
    </div>
  );
};

export default FormInputGroup;