import proj4 from "proj4";
import { IPolylineEntity } from "src/types/IDXFTypes";

export const ProcessPolylineEntity = (entity: IPolylineEntity, sourceProjection: string, targetProjection: string) => {
    if (entity.vertices.length === 0) {
        console.warn('POLYLINE entity missing vertices:', entity);
        return null;
    }

    const polylineCoordinates = entity.vertices.map(vertex => {
        return proj4(sourceProjection, targetProjection, [vertex.x, vertex.y, vertex.z || 0]);
    });

    return {
        type: 'PolylineFeature',
        geometry: {
            type: 'LineString',
            coordinates: polylineCoordinates,
        },
        properties: {
            layer: entity.layer,
            lineweight: entity.lineweight,
            is3dPolyline: entity.is3dPolyline,
            is3dPolygonMeshClosed: entity.is3dPolygonMeshClosed,
            is3dPolygonMesh: entity.is3dPolygonMesh,
            hasContinuousLinetypePattern: entity.hasContinuousLinetypePattern,
            includesCurveFitVertices: entity.includesCurveFitVertices,
            includesSplineFitVertices: entity.includesSplineFitVertices,
            isPolyfaceMesh: entity.isPolyfaceMesh,
            shape: entity.shape,
        },
    };
};
