import * as yup from "yup";

export enum AttributeTypes {
  TEXT = "TEXT",
  NUMBER = "NUMBER",
}

export const attributeTypeSchema = yup.object({
  id: yup.string().nullable().notRequired(),
  name: yup.string().required("attribute name is required"),
  type: yup.mixed().oneOf(Object.keys(AttributeTypes)).required(),
});

export interface IAttributeType
  extends yup.Asserts<typeof attributeTypeSchema> {}
