import proj4 from "proj4";
import { ILWPolylineEntity } from "src/types/IDXFTypes"; 

export const ProcessLwpolylineEntity = (entity: ILWPolylineEntity, sourceProjection: string, targetProjection: string) => {
    if (entity.vertices) {
        const polylineCoordinates = entity.vertices.map(vertex =>
            proj4(sourceProjection, targetProjection, [vertex.x, vertex.y])
        );
        return {
            type: 'LWPolylineFeature',
            geometry: {
                type: 'LineString',
                coordinates: polylineCoordinates,
            },
            properties: {               
                layer: entity.layer,
                lineweight: entity.lineweight,
                shape: entity.shape,
                ownerHandle: entity.ownerHandle,
                hasContinuousLinetypePattern: entity.hasContinuousLinetypePattern,
            },
        };
    }
    console.warn('LWPOLYLINE entity missing vertices:', entity);
    return null;
};