import { useAppSelector } from "src/app/hooks";
import HeaderNavbar from "src/components/header/header-nav/HeaderNavbar";
import ProfileMenu from "src/components/header/profile-menu/ProfileMenu";
import { selectContentTitle } from "src/state/slice/appSlice";
import { getCurrentFormattedDate } from "src/utils/date-utils/DateUtils";

export default function Header() {
  const currentFormattedDate: string = getCurrentFormattedDate();
  const contentTitle = useAppSelector(selectContentTitle);

  return (
    <div className="flex items-center justify-between pl-10">
      <div className="flex flex-col gap-y-1">
        <h1 className="text-4xl font-bold text-blue-500 capitalize">
          {contentTitle !== "" ? contentTitle : "default title"}
        </h1>
        <h1 className="font-semibold text-gray-500">{currentFormattedDate}</h1>
      </div>
      <div className="flex items-center gap-12 px-12">
        <HeaderNavbar />
        <ProfileMenu />
      </div>
    </div>
  );
}
