import proj4 from "proj4";
import { IPointEntity } from "src/types/IDXFTypes";

export const ProcessPointEntity = (entity: IPointEntity, sourceProjection: string, targetProjection: string) => {
    if (!entity.position) {
        console.warn('POINT entity missing position, skipping:', entity);
        return null;
    }
    
    const coordinates = proj4(sourceProjection, targetProjection, [entity.position.x, entity.position.y]);

    return {
        type: 'PointFeature',
        geometry: {
            type: 'Point',
            coordinates: coordinates,
        },
        properties: {
            layer: entity.layer,
            lineweight: entity.lineweight,
            handle: entity.handle,
            ownerHandle: entity.ownerHandle,
        },
    };
};
