import proj4 from "proj4";
import { ILineEntity } from "src/types/IDXFTypes"; 

export const ProcessLineEntity = (entity: ILineEntity, sourceProjection: string, targetProjection: string) => {
    if (entity.vertices) {
        const lineCoordinates = entity.vertices.map(vertex =>
            proj4(sourceProjection, targetProjection, [vertex.x, vertex.y])
        );
        return {
            type: 'LineFeature',
            geometry: {
                type: 'LineString',
                coordinates: lineCoordinates,
            },
            properties: {},
        };
    }
    console.warn('LINE entity missing vertices:', entity);
    return null;
};