import { createSlice } from "@reduxjs/toolkit";
import { RootState } from "src/app/store";

export enum ProjectionMethods {
  WGS84 = "WGS84",
  UTM = "UTM",
}

export interface projectState {
  selectedProjectId: string;
  selectedLayerId: string;
  shouldRefetchLayers: boolean;
  shouldRefetchAttributes: boolean;
}

const initialState: projectState = {
  selectedProjectId: "",
  selectedLayerId: "",
  shouldRefetchLayers: false,
  shouldRefetchAttributes: false,
};

export const projectSlice = createSlice({
  name: "project",
  initialState,
  reducers: {
    setSelectedProjectId: (state, action) => {
      state.selectedProjectId = action.payload;
    },
    clearSelectedProjectId: (state) => {
      state.selectedProjectId = "";
    },
    setSelectedLayerId: (state, action) => {
      state.selectedLayerId = action.payload;
    },
    setShouldRefetchLayers: (state, action) => {
      state.shouldRefetchLayers = action.payload;
    },
    setShouldRefetchAttributes: (state, action) => {
      state.shouldRefetchAttributes = action.payload;
    },
  },
});

export const selectSelectedProjectId = (state: RootState) => state.project.selectedProjectId;
export const selectShouldRefetchLayers = (state: RootState) => state.project.shouldRefetchLayers;
export const selectSelectedLayerId = (state: RootState) => state.project.selectedLayerId;
export const selectShouldRefetchAttributes = (state: RootState) => state.project.shouldRefetchAttributes;

export const {
  setSelectedProjectId,
  setSelectedLayerId,
  clearSelectedProjectId,
  setShouldRefetchLayers,
  setShouldRefetchAttributes,
} = projectSlice.actions;

export default projectSlice.reducer;
