import { combineReducers, configureStore } from "@reduxjs/toolkit";
import { persistReducer, persistStore } from "redux-persist";
import autoMergeLevel2 from "redux-persist/es/stateReconciler/autoMergeLevel2";
import storage from "redux-persist/lib/storage";
import appReducer from "src/state/slice/appSlice";
import projectReducer from "src/state/slice/projectSlice";
import geoJsonReducer from "src/state/slice/geoJsonSlice";
import coordinateReducer from "src/state/slice/coordinateSlice";

const persistConfig = {
  key: 'root',
  storage,
  stateReconciler: autoMergeLevel2
}

const rootReducer = combineReducers({
  app: appReducer,
  project: projectReducer,
  geoJson: geoJsonReducer,
  coordinate: coordinateReducer
});

const persistedReducer = persistReducer<ReturnType<typeof rootReducer>>(
  persistConfig,
  rootReducer
);

export const store = configureStore({
  reducer: persistedReducer,
});
export const persistor = persistStore(store);
export type AppDispatch = typeof store.dispatch;
export type RootState = ReturnType<typeof store.getState>;
