import { useAppDispatch, useAppSelector } from "src/app/hooks";
import FormInput from "src/components/form/FormInput";
import { IAttributeType } from "src/state/domains/attribute/types";
import { selectSelectedLayerId, setShouldRefetchAttributes } from "src/state/slice/projectSlice";
import useFetch from "src/utils/request/useFetch";
import { useState } from "react";
import { ILayer } from "src/state/domains/layer/types";
import axios from "axios";
import { useToast } from "src/utils/useToast";
import Button from "src/components/Button";
import { useSelector } from "react-redux";
import { selectLatitude, selectLongitude } from "src/state/slice/coordinateSlice";

export default function AttributeForm({ layer }: any) {
  const { successToast, errorToast } = useToast();
  const dispatch = useAppDispatch();
  const layerId = useAppSelector(selectSelectedLayerId);
  const [attributeValues, setAttributeValues] = useState<{ [key: number]: string }>({});
  const [isSavingChanges, setIsSavingChanges] = useState(false);
  const latitude = useSelector(selectLatitude);
  const longitude = useSelector(selectLongitude);

  const { data: attributeTypes }: { data: IAttributeType[] } = useFetch({
    url: `/layers/${layerId}/attribute-types`,
  });

  const handleAttributeChange = (attributeTypeId: number, value: string) => {
    setAttributeValues((prevValues) => ({
      ...prevValues,
      [attributeTypeId]: value,
    }));
  };

  const clearAttributeValues = () => {
    const clearedValues: { [key: number]: string } = {};
    Object.keys(attributeValues).forEach((key) => {
      clearedValues[Number(key)] = "";
    });
    setAttributeValues(clearedValues);
  };

  const handleSubmit = async () => {
    const attributeValuesData = Object.entries(attributeValues).map(([attributeTypeId, value]) => ({
      attributeTypeId: Number(attributeTypeId),
      value,
    }));

    const geometryData = {
      layerId,
      geometryType: layer?.layerType,
      coordinates: {
        type: "Point",
        coordinates: [latitude, longitude],
      },
    };

    const requestData = {
      geometryData,
      attributeValues: attributeValuesData,
    };

    setIsSavingChanges(true);
    try {
      await axios.post(`/geometries`, requestData);
      successToast("Attribute Created");
      dispatch(setShouldRefetchAttributes(true));
      clearAttributeValues();
    } catch (e) {
      errorToast("Failed to create attribute!");
    } finally {
      setIsSavingChanges(false);
    }
  };

  return (
    <div className="flex flex-col gap-y-4">
      {Array.isArray(attributeTypes) &&
        attributeTypes.map((attribute: IAttributeType) => (
          <div key={attribute.id}>
            <FormInput
              lable={attribute?.name as string}
              type={attribute.type as string}
              name={"layerName"}
              formValues={attributeValues}
              placeholder={attribute?.name}
              onChange={({ target: { name, value } }) =>
                handleAttributeChange(Number(attribute.id), value)
              }
            />
          </div>
        ))}
      <Button onClick={handleSubmit} isProcessing={isSavingChanges} disabled={isSavingChanges}>
        Submit
      </Button>
    </div>
  );
}