import proj4 from "proj4";
import { ITextEntity } from "src/types/IDXFTypes"; 

export const ProcessTextEntity = (entity: ITextEntity, sourceProjection: string, targetProjection: string) => {
    let coordinates;
    
    if (entity.vertices && entity.vertices.length > 0) {
        const textVertex = entity.vertices[0]; 
        coordinates = proj4(sourceProjection, targetProjection, [textVertex.x, textVertex.y]);
    } else if (entity.startPoint) {
        coordinates = proj4(sourceProjection, targetProjection, [entity.startPoint.x, entity.startPoint.y]);
    } else {
        console.warn('TEXT entity missing startPoint and vertices, skipping:', entity);
        return null;
    }

    return {
        type: 'TextFeature',
        geometry: {
            type: 'Point',
            coordinates: coordinates,
        },
        properties: {
            text: entity.text,
            textHeight: entity.textHeight,
            rotation: entity.rotation,
            lineweight: entity.lineweight,
        },
    };
};
