import classNames from "classnames";
import { NavLink } from "react-router-dom";
import { useAppDispatch } from "src/app/hooks";
import { clearSelectedProjectId } from "src/state/slice/projectSlice";

interface INavItemProps {
  link: string;
  name: string;
}

const NavItem = ({ link, name }: INavItemProps) => {
  const dispatch = useAppDispatch();
  const handleClick = () => {
    dispatch(clearSelectedProjectId());
  };

  return (
    <NavLink
      className={({ isActive }) =>
        classNames(
          "capitalize font-semibold px-4 py-2",
          isActive ? "bg-blue-500 text-white rounded-sm" : "text-blue-500 bg-white"
        )
      }
      to={link}
      onClick={handleClick}
    >
      {name}
    </NavLink>
  );
};

export default function HeaderNavbar() {
  return (
    <div>
      <ul className="flex items-center gap-6">
        <NavItem link="/home" name="Home" />
        <NavItem link="/users" name="Users" />
        <NavItem link="/projects" name="Projects" />
      </ul>
    </div>
  );
}
