import React, { useState, useEffect } from "react";
import { MapContainer, TileLayer, Marker, Popup, Polyline, Tooltip, useMapEvents } from "react-leaflet";
import "leaflet/dist/leaflet.css";
import L, { LatLngExpression } from "leaflet";
import { selectGeoJsonFeatures } from "src/state/slice/geoJsonSlice";
import { useDispatch, useSelector } from "react-redux";
import { setCoordinates, selectLatitude, selectLongitude } from "src/state/slice/coordinateSlice";
import icon from "leaflet/dist/images/marker-icon.png";
import iconShadow from "leaflet/dist/images/marker-shadow.png";

let DefaultIcon = L.icon({
  iconUrl: icon,
  shadowUrl: iconShadow,
});

L.Marker.prototype.options.icon = DefaultIcon;

interface ClickedPoint {
  lat: number;
  lng: number;
}

export interface Geometry {
  id: number;
  geometryType: string;
  coordinates: {
    type: string;
    coordinates: [number, number];
  };
}

interface MapComponentProps {
  height: string;
  width: string;
  zIndex?: number;
  geometries?: Geometry[];
  lines?: any[];
}

const MapClickHandler = () => {
  const dispatch = useDispatch();
  useMapEvents({
    click: (e) => {
      const { lat, lng } = e.latlng;
      dispatch(setCoordinates({ lat, lng }));
    },
  });
  return null;
};

const Map: React.FC<MapComponentProps> = ({
  height,
  width,
  zIndex,
  geometries
}) => {

  const features = useSelector(selectGeoJsonFeatures);
  const initialCenter: LatLngExpression = [7.8731, 80.7718];
  const initialZoom = 8;
  const maxZoomLevel = 50;
  
  const [clickedPoints, setClickedPoints] = useState<ClickedPoint[]>([]);
  
  const latitude = useSelector(selectLatitude);
  const longitude = useSelector(selectLongitude);

  useEffect(() => {
    if (latitude !== null && longitude !== null) {
      setClickedPoints(currentPoints => [...currentPoints, { lat: latitude, lng: longitude }]);
    }
  }, [latitude, longitude]);

  return (
    <div style={{ height, width, position: "relative", zIndex }}>
      <MapContainer
        center={initialCenter}
        zoom={initialZoom}
        maxZoom={maxZoomLevel}
        style={{ height: "100%", width: "100%" }}
      >
        <MapClickHandler />
        <TileLayer
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
          attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
        />
        {geometries?.map((geometry) => (
          <Marker key={geometry.id} position={geometry.coordinates.coordinates}>
            <Popup>
              <div>
                <p>ID: {geometry.id}</p>
                <p>Geometry Type: {geometry.geometryType}</p>
              </div>
            </Popup>
          </Marker>
        ))}
        {latitude && longitude && (
          <Marker position={[latitude, longitude]}>
            <Popup>Clicked Marker</Popup>
          </Marker>
        )}
        {features.map((feature, index) => {
          switch (feature.geometry.type) {
            case 'LineString':
              const polylinePositions = feature.geometry.coordinates.map(coord =>
                [coord[1], coord[0]] as LatLngExpression
              );
              return <Polyline key={index} positions={polylinePositions} />;

            case 'Point':
              const markerPosition: LatLngExpression = [
                feature.geometry.coordinates[1],
                feature.geometry.coordinates[0],
              ];

              const customIcon = L.divIcon({
                className: 'custom-div-icon',
                html: `<div style="background-color: white; padding: 5px; border-radius: 5px;">${feature.properties?.text || `Text ${index + 1}`}</div>`,
                iconSize: [30, 30],
                iconAnchor: [15, 15]
              });
              return (
                <Marker key={index} position={markerPosition} icon={customIcon} />
              );
            default:
              return null;
          }
        })}
      </MapContainer>
    </div>
  );
};

export default Map;
