import { createSlice } from "@reduxjs/toolkit";
import { RootState } from "src/app/store";

export interface AppState {
  selectContentTitle: string;
  isOpenCreateFormModal: boolean;
}

const initialState: AppState = {
  selectContentTitle: "",
  isOpenCreateFormModal: false,
};

export const appSlice = createSlice({
  name: "app",
  initialState,
  reducers: {
    setContentTitle: (state, action) => {
      state.selectContentTitle = action.payload;
    },

    setIsOpenCreateFormModal: (state, action) => {
      state.isOpenCreateFormModal = action.payload;
    },
  },
});

export const selectAppConfigurations = (state: RootState) => state.app;
export const selectContentTitle = (state: RootState) =>
  state.app.selectContentTitle;
export const selectIsOpenCreateFormModal = (state: RootState) =>
  state.app.isOpenCreateFormModal;
export const { setContentTitle, setIsOpenCreateFormModal } = appSlice.actions;
export default appSlice.reducer;
