import proj4 from "proj4";
import { IMTextEntity } from "src/types/IDXFTypes"; 

export const ProcessMTextEntity = (entity: IMTextEntity, sourceProjection: string, targetProjection: string) => {
    if (!entity.position) {
        console.warn('MTEXT entity missing position, skipping:', entity);
        return null;
    }
    
    const coordinates = proj4(sourceProjection, targetProjection, [entity.position.x, entity.position.y]);

    return {
        type: 'MTextFeature',
        geometry: {
            type: 'Point',
            coordinates: coordinates,
        },
        properties: {
            text: entity.text,
            height: entity.height,
            width: entity.width,
            color: entity.color,
            colorIndex: entity.colorIndex,
            directionVector: entity.directionVector,
            drawingDirection: entity.drawingDirection,
            attachmentPoint: entity.attachmentPoint,
            layer: entity.layer,
            handle: entity.handle,
            ownerHandle: entity.ownerHandle,
        },
    };
};
