import { IDXFData } from "src/types/IDXFTypes";
import { ProcessLineEntity } from "src/components/dxf/EntityConversion/ProcessLineEntity";
import { ProcessLwpolylineEntity } from "src/components/dxf/EntityConversion/ProcessLwpolylineEntity";
import { ProcessTextEntity } from "src/components/dxf/EntityConversion/ProcessTextEntity";
import { ProcessMTextEntity } from "src/components/dxf/EntityConversion/ProcessMTextEntity";
import { ProcessPointEntity } from "src/components/dxf/EntityConversion/ProcessPointEntity";
import { ProcessPolylineEntity } from "src/components/dxf/EntityConversion/ProcessPolylineEntity";

const DXFImport = (dxfData: IDXFData, projectionType: string) => {
    
    let sourceProjection: any;
    let targetProjection = "+proj=longlat +datum=WGS84 +no_defs";

    if ( projectionType === 'WGS84') {
        sourceProjection = "+proj=utm +zone=44 +datum=WGS84 +units=m +no_defs";
    } else if (projectionType === 'SLD99') {
        sourceProjection = "+proj=tmerc +lat_0=7.00042152777778 +lon_0=80.7735530833333 +k=0.9999238418 +x_0=500000 +y_0=500000 +ellps=evrst30 +towgs84=-0.293,766.95,87.713,-0.195704,-1.695068,-3.473016,-0.039338 +units=m +no_defs +type=crs";
    }

    const geoJSONFeatures = dxfData.entities
        .flatMap((entity) => {
            switch (entity.type) {
                case 'POINT':
                    return ProcessPointEntity(entity, sourceProjection, targetProjection);
                case 'LINE':
                    return ProcessLineEntity(entity, sourceProjection, targetProjection);
                case 'LWPOLYLINE':
                    return ProcessLwpolylineEntity(entity, sourceProjection, targetProjection);
                case 'POLYLINE':
                    return ProcessPolylineEntity(entity, sourceProjection, targetProjection);
                case 'TEXT':                  
                    return ProcessTextEntity(entity, sourceProjection, targetProjection);
                case 'MTEXT':
                    return ProcessMTextEntity(entity, sourceProjection, targetProjection);
                case 'CIRCLE':
                    return null;
                    // Convert circle to GeoJSON (possibly as Point with radius property)
                case 'ELLIPSE':
                    return null;
                    // Convert ellipse to GeoJSON (more complex, possibly as a Polygon)
                default:
                    return null;
            }
        })
        .filter(Boolean);

    return {
        type: 'FeatureCollection',
        features: geoJSONFeatures,
    };
};

export default DXFImport;
