import { createSlice } from '@reduxjs/toolkit';
import { RootState } from 'src/app/store';

interface CoordinatesState {
  latitude: number | null;
  longitude: number | null;
}

const initialState: CoordinatesState = {
  latitude: null,
  longitude: null,
};

export const coordinatesSlice = createSlice({
  name: 'coordinates',
  initialState,
  reducers: {
    setCoordinates: (state, action) => {
      state.latitude = action.payload.lat;
      state.longitude = action.payload.lng;
    },
    clearCoordinates: (state) => {
      state.latitude = null;
      state.longitude = null;
    },
  },
});

export const selectLatitude = (state: RootState) => state.coordinate.latitude;
export const selectLongitude = (state: RootState) => state.coordinate.longitude;

export const { setCoordinates, clearCoordinates } = coordinatesSlice.actions;

export default coordinatesSlice.reducer;
