import axios from "axios";
import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { storage } from "src/firebase/firebaseConfig";
import { ref, uploadBytes, getDownloadURL } from "firebase/storage";
import { useToast } from "src/utils/useToast";
import { useAppDispatch, useAppSelector } from "src/app/hooks";
import Button from "src/components/Button";
import PageWrap from "src/components/PageWrap";
import FormInput from "src/components/form/FormInput";
import Map, { Geometry } from "src/components/map/Map";
import Modal from "src/components/modal/Modal";
import { ILayer } from "src/state/domains/layer/types";
import { IProject } from "src/state/domains/project/types";
import {
  selectIsOpenCreateFormModal,
  setIsOpenCreateFormModal,
} from "src/state/slice/appSlice";
import useFetch from "src/utils/request/useFetch";
import FormFileUpload from "src/components/form/FormFileUpload";
import { FileUpload } from "src/components/dxf/FileUpload";
import FormSelect from "src/components/form/FormSelect";
import { LayerTypes } from "src/state/domains/layer-types/types";
import { ProjectionMethods } from "src/state/domains/projection-methods/types";
import FormInputGroup from "src/components/form/FormInputGroup";
import { AttributeTypes } from "src/state/domains/attribute/types";
import { selectSelectedLayerId, selectShouldRefetchAttributes, setSelectedLayerId, setShouldRefetchAttributes, setShouldRefetchLayers } from "src/state/slice/projectSlice";
import AttributeForm from "src/pages/layers/attribute/attribute-form";

const defaultLayer: ILayer = {
  layerName: "",
  layerType: "",
  attributeTypes: [],
  description: "",
  dxfUrl: "",
  projectionType: "",
  projectId: "",
};

export default function ProjectView() {
  const { successToast, errorToast } = useToast();
  const dispatch = useAppDispatch();
  const shouldRefetchAttribute = useAppSelector(selectShouldRefetchAttributes);
  const layerId = useAppSelector(selectSelectedLayerId);
  const { projectId } = useParams<{ projectId: string }>();
  const { data: project }: { data: IProject } = useFetch({
    url: `/projects/${projectId}`,
  });
  const isModalOpen = useAppSelector(selectIsOpenCreateFormModal);

  const [layer, setLayer] = useState<ILayer>(defaultLayer);
  const [isSavingChanges, setIsSavingChanges] = useState(false);
  const [isUploading, setIsUploading] = useState<boolean>(false);
  const [isSuccess, setIsSuccess] = useState<boolean>(false);

  const { data: geometryList, reFetch } = useFetch({
    url: `/layers/${layerId}/geometries`,
  });

  const { data: layerData, reFetch: reFetchLayers, loading } : {data: ILayer, reFetch: any, loading: boolean }= useFetch({
    url: `/layers/${layerId}`,
  });

  useEffect(() => {
    if (shouldRefetchAttribute) {
      reFetch();
      dispatch(setShouldRefetchAttributes(false));
    }
  }, [shouldRefetchAttribute, reFetch, dispatch]);

  const geometries = geometryList?.data?.map((item: Geometry) => ({
    id: item.id,
    geometryType: item.geometryType,
    coordinates: { type: item.coordinates.type, coordinates: item.coordinates.coordinates },
  })) || [];

  const handleInputGroupChange = (index: any, name: any, value: any) => {
    let updatedAttributeTypes = [...(layer.attributeTypes || [])];
    if (index < updatedAttributeTypes.length) {
      updatedAttributeTypes[index] = {
        ...updatedAttributeTypes[index],
        [name]: value,
      };
    } else {
      updatedAttributeTypes.push({ name: "", type: AttributeTypes.NUMBER });
    }
    setLayer({ ...layer, attributeTypes: updatedAttributeTypes });
  };

  const handleFileChange = (event: any) => {
    const file = event.target.files[0];
    if (!file) return;

    try {
      setIsUploading(true);
      const storageRef = ref(storage, `${file.name}`);
      const uploadFile = async () => {
        try {
          await uploadBytes(storageRef, file);
          const url = await getDownloadURL(storageRef);
          setLayer((prevLayer) => ({
            ...prevLayer,
            dxfUrl: url,
          }));
          setIsUploading(false);
          setIsSuccess(true);
        } catch (error) {
          console.error("Error uploading file:", error);
        }
      };
      uploadFile();
    } catch (error) {
      console.error("Error uploading file:", error);
    }
  };

  const handleFormChange = (name: string, value: string) => {
    setLayer((preLayer) => ({
      ...preLayer,
      [name]: value,
    }));
  };

  const handleFormSubmit = async () => {
    setIsSavingChanges(true);
    const layerObj = {
      ...layer,
      projectId: projectId as string,
    };
    
    try {
      const response = await axios.post(`/layers`, layerObj);
      const newLayerId = response.data.id;
      successToast("Layer Created");
      dispatch(setShouldRefetchLayers(true));
      dispatch(setIsOpenCreateFormModal(false));
      dispatch(setSelectedLayerId(newLayerId));
      setLayer(defaultLayer);
      reFetchLayers();
    } catch {
      errorToast("Failed to create project!");
    } finally {
      setIsSavingChanges(false);
    }
  };

  return (
    <PageWrap title={project?.projectName}>
      <div className="h-[calc(100vh-112px)] w-[100%] ">
        <Map
          height="100%"
          width="100%"
          zIndex={10}
          geometries={geometries}
        />
      </div>
      <Modal
        title="Add new layer"
        isOpen={isModalOpen}
        onClose={() => {
          dispatch(setIsOpenCreateFormModal(false));
          setLayer(defaultLayer);
        }}
      >
        <div className="bg-white py-6 px-6 space-y-10">
          <form action="" className="space-y-4">
            <FormInput
              lable="Layer name"
              type="text"
              name="layerName"
              formValues={layer}
              placeholder="Layer Name"
              onChange={({ target: { name, value } }) =>
                handleFormChange(name, value)
              }
            />
            <FormSelect
              lable="Layer type"
              name="layerType"
              formValues={layer}
              items={Object.keys(LayerTypes).map((lb) => ({
                value: lb,
                displayValue: lb,
              }))}
              onChange={({ target: { name, value } }) =>
                handleFormChange(name, value)
              }
            />
            <FormInput
              lable="Description"
              type="text"
              name="description"
              formValues={layer}
              placeholder="Description"
              onChange={({ target: { name, value } }) =>
                handleFormChange(name, value)
              }
            />
            <FormFileUpload
              lable="Upload DXF File"
              name="myfile"
              onChange={handleFileChange}
              isUploading={isUploading}
              isSuccess={isSuccess}
            />
            {isSuccess && (
              <FormSelect
                lable="Projection Type"
                name="projectionType"
                formValues={layer}
                items={Object.keys(ProjectionMethods).map((lb) => ({
                  value: lb,
                  displayValue: lb,
                }))}
                onChange={({ target: { name, value } }) =>
                  handleFormChange(name, value)
                }
              />
            )}
            <FormInputGroup
              addInputButtonLable="Add attribute"
              formInputLable="Attribute"
              formInputValues={layer}
              formInputPlaceholder="Attribute name"
              formSelectPlaceholder="Data type"
              onInputChange={(index, event) =>
                handleInputGroupChange(
                  index,
                  event.target.name,
                  event.target.value
                )
              }
              onSelectChange={(index, event) =>
                handleInputGroupChange(
                  index,
                  event.target.name,
                  event.target.value
                )
              }
              formSelectItems={Object.keys(AttributeTypes).map((at) => ({
                value: at,
                displayValue: at,
              }))}
            />
          </form>
          <div className="flex justify-end">
            <Button
              onClick={handleFormSubmit}
              isProcessing={isSavingChanges}
              disabled={isSavingChanges}
            >
              Submit
            </Button>
          </div>
        </div>
      </Modal>
      {layerData?.dxfUrl && !loading && <FileUpload dxfFileUrl={layerData.dxfUrl} projectionType={layerData?.projectionType as string}/>}
      <Modal title="Attributes" isOpen={true} className="right-10" width="w-[350px]" height="h-full">
        <AttributeForm layer={layerData} />
      </Modal>
    </PageWrap>
  );
}
