import { XMarkIcon } from "@heroicons/react/24/solid";

interface IModalProps {
  isOpen: boolean;
  onClose?: () => void;
  title: string;
  children: JSX.Element;
  className?: string;
  width?: string;
  height?: string;
}

const Modal = ({
  isOpen,
  onClose,
  title,
  children,
  className = "left-10",
  width = "w-[500px]",
  height = "h-auto",
}: IModalProps) => {
  return (
    <>
      {isOpen && (
        <div
          className={`absolute top-10 ${className} ${width} ${height} z-50  h-auto  bg-white transition-opacity duration-200 rounded-md overflow-auto`}
        >
          <div className="px-8 py-4 flex items-center justify-between">
            <span className="text-xl capitalize text-blue-500 font-bold ">
              {title}
            </span>
            {onClose && (
              <button
                className="p-0.5 border border-gray-400 rounded"
                onClick={onClose}
              >
                <XMarkIcon className="w-5 h-5 text-gray-600" />
              </button>
            )}
          </div>
          <div className="px-8 py-4 max-h-[600px]  overflow-auto">
            {children}
          </div>
        </div>
      )}
    </>
  );
};
export default Modal;
