import { useLocation, useNavigate } from "react-router-dom";
import { IProject } from "src/state/domains/project/types";
import defaultProjectImg from "src/assets/defaultProjectImg.jpg";
import { PlusIcon } from "@heroicons/react/24/solid";
import PageWrap from "src/components/PageWrap";
import useFetch from "src/utils/request/useFetch";
import { useAppDispatch } from "src/app/hooks";
import { setSelectedProjectId } from "src/state/slice/projectSlice";

export default function ViewProject() {
  const navigate = useNavigate();
  const { pathname } = useLocation();
  const dispatch = useAppDispatch();

  const { data: projects }: { data: IProject[] } = useFetch({
    url: `/projects`,
  });

  return (
    <PageWrap title="recent projects">
      <div className="grid grid-cols-4 justify-items-center gap-y-12 py-10">
        <div className="flex flex-col gap-y-1 shadow-md">
          <h1 className="text-blue-500 capitalize">Add new project</h1>
          <button
            className="w-72 h-48 bg-white rounded-md border border-blue-600 flex items-center justify-center"
            onClick={() => navigate(`${pathname}/create`)}
          >
            <PlusIcon className="w-20 h-20 text-gray-300" />
          </button>
        </div>
        {Array.isArray(projects) &&
          projects?.map((project) => (
            <button
              key={project.id}
              className="text-blue-500 flex flex-col gap-y-1 shadow-md"
              onClick={() => {
                dispatch(setSelectedProjectId(project.id));
                navigate(`${pathname}/${project.id}`);
              }}
            >
              <h1 className="capitalize">{project.projectName}</h1>
              <img
                src={defaultProjectImg}
                className="w-72 h-48 rounded-md"
                alt="projectThumbnail"
              />
            </button>
          ))}
      </div>
    </PageWrap>
  );
}
