import { createSlice } from '@reduxjs/toolkit';
import { RootState } from 'src/app/store';

export interface GeoJsonState {
    features: GeoJSON.Feature[];
  }
  
  const initialState: GeoJsonState = {
    features: [],
  };

export const geoJsonSlice  = createSlice({
  name: 'lines',
  initialState,
  reducers: {
    setFeatures: (state, action) => { 
      state.features = action.payload;
    },
  },
});

export const selectGeoJsonFeatures = (state: RootState) => state.geoJson.features;

// Export the actions and the reducer
export const { setFeatures } = geoJsonSlice.actions;
export default geoJsonSlice.reducer;
