import { EyeIcon, EyeSlashIcon } from "@heroicons/react/24/outline";
import classNames from "classnames";
import React, { ForwardedRef, PropsWithChildren, useState } from "react";
import { IFormInputProps } from "src/components/types/form/types";

function FormInput(
  {
    lable,
    type = "text",
    name,
    value,
    formValues,
    placeholder,
    required,
    formErrors,
    onChange,
    onFocus,
    showErrors,
    size = "normal",
    showLabel = true,
    showShadow = true,
    disabled,
    onKeyDown,
    showPasswordVisibilityIcon = false,
    className,
    ...rest
  }: PropsWithChildren<IFormInputProps>,
  ref: ForwardedRef<HTMLInputElement | null>,
) {
  const hasError = showErrors && formErrors && formErrors[name];
  const [inputType, setInputType] = useState(type);

  const handleToggleType = () => {
    if (inputType === "password") {
      setInputType("text");
    } else {
      setInputType("password");
    }
  };

  return (
    <>
      <div
        className={classNames("flex flex-col gap-y-1", {
          "shadow-sm": showShadow,
        })}
      >
        <label htmlFor={name} className="text-blue-500 capitalize">
          {showLabel && <span className="capitalize">{lable}</span>}
        </label>
        <input
          ref={ref}
          data-testid={name}
          disabled={disabled}
          id={name}
          type={inputType}
          name={name}
          value={
            name
              .split(".")
              .reduce((a, b) => a[b], formValues) as unknown as string
          }
          className={classNames(
            "px-2 py-2 border border-blue-500 rounded-md",
            { "shadow-sm": showShadow },
            { "py-3": size === "normal" },
            { "pl-3": showPasswordVisibilityIcon },
            className && className
          )}
          placeholder={placeholder}
          required={required}
          onChange={(e) => onChange?.(e, e.target.value)}
          onFocus={onFocus}
          onKeyDown={onKeyDown}
          {...rest}
        />
        {showPasswordVisibilityIcon && (
          <button
            type="button"
            className="absolute inset-y-1 right-0 pr-3 pt-6 flex items-center"
            onClick={handleToggleType}
          >
            {inputType === "password" ? (
              <EyeIcon className="h-5 w-5 text-gray-400" />
            ) : (
              <EyeSlashIcon className="h-5 w-5 text-gray-400" />
            )}
          </button>
        )}
      </div>
      {hasError && (
        <p className="mt-1 text-sm text-red-600" id="email-error">
          {/* Possibly make this configurable to show multiple formErrors */}
          {formErrors && formErrors[name]}
        </p>
      )}
    </>
  );
}
export default React.forwardRef(FormInput);