import { IFormSelectProps } from "src/components/types/form/types";
import classNames from "classnames";

function FormSelect({
  lable,
  placeholder,
  name,
  items,
  formValues,
  formErrors,
  onChange,
  className,
  ...rest
}: IFormSelectProps) {
  const hasError = formErrors && formErrors[name]?.length > 0;

  return (
    <div className="flex flex-col gap-y-1 w-full">
      <label htmlFor={lable} className="text-blue-500 capitalize">
        {lable}
      </label>
      <select
        className={classNames(
          "px-2 py-2.5 border border-blue-500 rounded-md bg-white",
          className && className,
        )}
        name={name}
        id={name}
        value={
          name
            .split(".")
            .reduce((a, b) => a[b], formValues) as unknown as string
        }
        onChange={onChange}
        {...rest}
      >
        <option value="" disabled selected={!formValues[name]}>
          Select {placeholder ? placeholder : "an option"}
        </option>
        {items.map(({ value, displayValue }) => (
          <option key={value} value={value}>
            {displayValue}
          </option>
        ))}
      </select>
      {hasError && (
        <p className="mt-1 text-sm text-red-600 lowercase" id="email-error">
          {formErrors && formErrors[name]}
        </p>
      )}
    </div>
  );
}

export default FormSelect;
